<template>
  <div>
    <el-dialog
      title="处理"
      :visible.sync="wareHouseFormVisible"
    >
      <span
        class="delete-contract"
        @click="deleteRecord('合同')"
      >删除该合同？</span>
      <span class="dialog-title">平仓信息</span>
      <el-form
        :model="formData"
        :inline="true"
        ref="formData"
      >
        <el-form-item
          label="仓单编号"
          :label-width="formLabelWidth"
          style="width:365px;"
        >
          {{form.wareHouseId}}
        </el-form-item>
      </el-form>
      <el-form
        :model="formData"
        :inline="true"
        :rules="rules"
        ref="formData"
      >
        <el-form-item
          label="交易方式"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="txType"
        >
          <el-select
            v-model="formData.txType"
            placeholder="请选择交易方式"
            :disabled="liquidated"
          >
            <el-option
              label="回购"
              value="1"
            ></el-option>
            <el-option
              label="抛售"
              value="2"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="平仓总价"
          :label-width="formLabelWidth"
          prop="liquidateValue"
        >
          <el-input
            v-model="formData.liquidateValue"
            :disabled="liquidated"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="平仓时间"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="time"
        >
          <el-date-picker
            type="datetime"
            placeholder="选择日期时间"
            v-model="formData.time"
            style="width: 100%;"
            :disabled="liquidated"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="平仓时间期货价格"
          :label-width="formLabelWidth"
          prop="liquidatePrice"
        >
          <el-input
            v-model="formData.liquidatePrice"
            :disabled="liquidated"
          ></el-input>
        </el-form-item>
      </el-form>
      <div class="buttonGroup">
        <el-button
          type="primary"
          @click="liquidate('formData')"
          v-if="!liquidated"
        >确认平仓</el-button>
        <el-button
          type="primary"
          @click="liquidate('formData')"
          v-else
        >确认还款并归档</el-button>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="wareHouseFormVisible = false">取 消</el-button>
        <!-- <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button> -->
      </div>
    </el-dialog>

    <el-dialog
      title="处理"
      :visible.sync="warningOperateVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="价值预警">
          <table class="value-warning">
            <tr>
              <th>放款金额</th>
              <td>2</td>
              <!-- <th>&nbsp;</th>
              <td>&nbsp;</td> -->
            </tr>
            <tr>
              <th>仓单原价值</th>
              <td>4</td>
              <th>仓单现价值</th>
              <td>6</td>
            </tr>
            <tr>
              <th>原保证金</th>
              <td>8</td>
              <th>现保证金</th>
              <td>10</td>
            </tr>
          </table>
        </el-tab-pane>
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加1000吨的货物或65040000元保证金。
          </div>
        </el-tab-pane>
        <el-tab-pane label="风险处理">
          <div class="program">
            客户实际追加
            <el-input
              v-model="programForm.weight"
              clearable
              style="width:120px"
            >
            </el-input>
            吨的货物，
            <el-input
              v-model="programForm.ammount"
              clearable
              style="width:120px"
            >
            </el-input>
            元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../validate.js";
export default {
  props: ["dialogFormVisible", "form", "dialogFormVisible1"],
  data() {
    return {
      formData: {
        wareHouseId: "", //仓单编号
        time: "", //平仓时间
        txType: "", //交易方式
        liquidatePrice: "", //平仓时间期货价格
        liquidateValue: "" //平仓总价
      },
      programForm: {
        weight: "",
        ammount: ""
      },

      wareHouseFormVisible: this.dialogFormVisible,
      warningOperateVisible: this.dialogFormVisible1,
      formLabelWidth: "135px",
      rules: rules,

      liquidated: false //判断是否平仓
    };
  },
  watch: {
    dialogFormVisible(val) {
      this.wareHouseFormVisible = val; //新增result的watch，监听变更并同步到myResult上
    },
    dialogFormVisible1(val) {
      this.warningOperateVisible = val;
    },
    wareHouseFormVisible(val) {
      this.$emit("dialogFormVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    },
    warningOperateVisible(val) {
      this.$emit("warningOperateVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    }
  },
  methods: {
    liquidate(formData) {
      this.$refs[formData].validate(valid => {
        if (valid) {
          this.liquidated = true;
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    deleteRecord(type){
      this.$emit("deleteRecord", type);
    }
  },
  components: {}
};
</script>

<style>
.delete-contract {
  display: block;
  margin-bottom: 15px;
  width: 85px;
}
.delete-contract:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
}
.value-warning {
  border-spacing: 0;
  border-collapse: collapse;
}
.value-warning tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.value-warning tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
.dialog-button {
  float: right;
}
</style>



