        contactMember: "",
        contactNum: "",
        truth: ""
      },
      selectlistRow: [],
      selectlistRow1: [],

      currentPage: 1,
      pageSize: 1,
      totalPage: 100,

      dialogFormVisible: false,
      addContractVisible: false,
      warehouseVisible: false,
      warehouseInnerVisible: false,
      accountRecordVisible: false,
      monitorVisible: false,
      methodVisible: false,

      liquidated: false, //判断是否平仓

      form: {
        //处理页面表单
        // wareHouseId: "", //仓单编号
        // customerId: "", //交易帐户
        // time: "", //平仓时间
        // txType: "", //交易方式
        // liquidatePrice: "", //平仓时间期货价格
        // liquidateValue: "" //平仓总价
      },
      formLabelWidth: "135px",
      pageType: 1,

      state: "",
      wareHouse: [],
      timeout: null,

      recordImages:['../../static/images/test1.jpg','../../static/images/test2.jpg','../../static/images/test3.jpg'],
      fileList: [],
      rules: rules
    };
  },
  mounted() {
    this.wareHouse = this.loadWarehouseId();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo() {
      console.log(this.searchForm.option + "+" + this.searchForm.inputText);
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    //根据类型打开dialog
    openDialog(row, type) {
      if (type == "wareHouse") {
        this.warehouseVisible = true;
      } else if (type == "operate") {
        this.form = row;
        this.dialogFormVisible = true;
      } else if (type == "contract") {
        this.addContractVisible = true; //打开添加合同面板
      } else if (type == "account") {
        this.accountRecordVisible = true;
      } else if (type == "monitor") {
        this.monitorVisible = true;
      } else if (type == "method") {
        this.methodVisible = true;
      }
    },
    dialogFormVisibleChange(val) {
      this.dialogFormVisible = val;
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    //删除合同或仓单
    deleteRecord(type) {
      if (type == "合同") {
        this.$confirm("此操作删除该合同, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.dialogFormVisible = false;
            this.$router.push("/GetContract");
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else if (type == "仓单") {
        this.$confirm("此操作删除该仓单, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.warehouseVisible = false;
            this.$router.push("/GetContract");
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    //操作状态处理
    statusSwitch(row, column) {
      switch (row.status) {
        case "0":
          return "0";
          break;

        case "1":
          return "存续";
          break;
      }
    },
    // 获取表格选中时的数据
    selectRow(val) {
      this.selectlistRow = val;
    },
    // 获取表格选中时的数据
    selectRow1(val) {
      this.selectlistRow1 = val;
    },
    // 增加行
    addRow() {
      let list = {
        rowNum: this.wareHouseTable.length + 1,
        address: this.address,
        name: this.name,
        weather: this.weather,
        phone: this.phone,
        date: this.date,
        mdate: this.mdate,
        loveer: this.loveer
      };
      this.wareHouseTable.unshift(list);
    },
    // 增加行
    addRow1() {
      let list = {
        rowNum: this.addWareHouseTable.length + 1,
        address: this.address,
        name: this.name,
        weather: this.weather,
        phone: this.phone,
        date: this.date,
        mdate: this.mdate,
        loveer: this.loveer
      };
      this.addWareHouseTable.unshift(list);
    },
    // 删除方法
    // 删除选中行
    delData() {
      for (let i = 0; i < this.selectlistRow.length; i++) {
        let val = this.selectlistRow;
        // 获取选中行的索引的方法
        // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
        // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
        val.forEach((val, index) => {
          this.wareHouseTable.forEach((v, i) => {
            if (val.rowNum === v.rowNum) {
              // i 为选中的索引
              this.wareHouseTable.splice(i, 1);
            }
          });
        });
      }
      // 删除完数据之后清除勾选框
      this.$refs.wareHouseTable.clearSelection();
    },
    // 删除方法
    // 删除选中行
    delData1() {
      for (let i = 0; i < this.selectlistRow1.length; i++) {
        let val = this.selectlistRow1;
        // 获取选中行的索引的方法
        // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
        // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
        val.forEach((val, index) => {
          this.addWareHouseTable.forEach((v, i) => {
            if (val.rowNum === v.rowNum) {
              // i 为选中的索引
              this.addWareHouseTable.splice(i, 1);
            }
          });
        });
      }
      // 删除完数据之后清除勾选框
      this.$refs.addWareHouseTable.clearSelection();
    },
    //远程搜索 下拉搜索功能
    querySearchAsync(queryString, cb) {
      var wareHouse = this.wareHouse;
      var results = queryString
        ? wareHouse.filter(this.createStateFilter(queryString))
        : wareHouse;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 300 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    handleSelect(item) {
      alert(item.value);
    },
    loadWarehouseId() {
      return [
        { value: "三全鲜食（北新泾店）", address: "长宁区新渔路144号" },
        { value: "三全鲜食（北新泾店）", address: "长宁区新渔路144号" }
      ];
    },

    //上传功能
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },

    //删除轮播图
    deletePayRecord(){
      console.log(this.recordImages[this.$refs.payCarousel.activeIndex])
    }
  },
  components: {
    vSearch,
    handleDialog
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.dialog-title {
  font-size: 20px;
  margin-bottom: 20px;
  display: block;
}
.buttonGroup {
  text-align: center;
  margin-left: -50px;
  margin-top: 25px;
}
.warehouseNum {
  float: left;
}
.head-tool {
  position: absolute;
  top: 24px;
  left: 105px;
}
.delete-warehouse {
  margin-bottom: 15px;
  margin-left: 15px;
  width: 84px;
  float: left;
}
.delete-warehouse:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
}
.selectSearch {
  float: left;
  margin-left: 15px;
  margin-top: -5px;
}
.addWarehouse {
  margin-left: 15px;
  margin-top: -5px;
}
.dialog-info {
  font-size: 16px;
  margin-bottom: 20px;
}
.dialog-name {
  color: blue;
}
.recordInfo {
  margin-bottom: 10px;
  font-size: 16px;
}
.el-upload--text {
  width: 80px;
  height: 32px;
  border: 0px;
  margin-top: 5px;
}
.warehouseInfo {
  border-spacing: 0;
  border-collapse: collapse;
}
.warehouseInfo tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.warehouseInfo tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
</style>



